.cartPage {
  padding: 5vmax;
}

.cartHeader {
  background-color: tomato;
  width: 90%;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
  color: #fff;
  margin: auto;
  font: 500 1.2vmax "Roboto";
}

.cartHeader > p {
  margin: 10px;
}

.cartHeader > p:last-child {
  text-align: end;
}
.cartItems {
  height: 50vh;
  overflow-y: auto;
}

.cartContainer {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
}
.cartInput {
  display: flex;
  align-items: center;
  height: 8vmax;
}

.cartSubTotal {
  display: flex;
  padding: 0.5vmax;
  justify-content: flex-end;
  color: rgba(0, 0, 0, 0.753);
  box-sizing: border-box;
  font: 300 1.2vmax cursive;
  align-items: center;
}

.cartGrossProfit {
  display: grid;
  grid-template-columns: 2fr 1.2fr;
}

.cartGrossProfitBox {
  border-top: 3px solid tomato;
  box-sizing: border-box;
  margin: 1vmax 4vmax;
  display: flex;
  justify-content: space-between;
  font: 300 1.2vmax cursive;
  padding: 2vmax 0.5vmax;
}
.checkOutBtn {
  display: flex;
  justify-content: flex-end;
  box-sizing: border-box;
  margin: 0 3vmax;
}

.checkOutBtn > button {
  padding: 0.8vmax 4vmax;
  width: 50%;
  font: 300 1.2vmax "Roboto";
}

.emptyCart {
  margin: auto;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: flex;
  height: 50vh;
  padding: 10vmax;
  transition: all 0.5s;
  

}
.emptyCart > svg {
  font-size: 5vmax;
  color: tomato;
  animation-name:creating;
  animation-duration: 0.5s;
  transform-origin: center;
  transition: all 0.5s;
 
}
@keyframes creating {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
.emptyCart > p {
  font: 300 2vmax "Roboto";
  margin: 1vmax;
}


.emptyCart > a {
  background-color: rgb(51, 51, 51);
  color: #fff;
  font: 400 1vmax "Roboto";
  text-decoration: none;
  padding: 1vmax 3vmax;
  border: none;
  cursor: pointer;
  transition: all 0.1s;
}
.emptyCart > a:hover {
  background-color: rgb(5, 5, 5);
}
@media screen and (max-width: 600px) {
  .cartPage {
    padding: 0;
    min-height: 65vh;
    margin-top: 8vmax;
  }
  .cartHeader {
    width: 100%;
    padding: 2vmax 0;
    font: 300 1.7vmax "Roboto";
    grid-template-columns: 3fr 1fr 1fr;
    margin-bottom: 2vmax;
    position: fixed;
    top: 0;
    left: 0;
  }
  .cartItems {
    height: 50vh;
    overflow-y: auto;
  }
  .cartContainer {
    width: 100%;
    grid-template-columns: 3fr 1fr 1fr;
  }
  .cartInput {
    height: 20vmax;
  }

  .cartSubTotal {
    padding: 1.5vmax;
    height: 20vmax;
    font: 300 2vmax cursive;
  }
  .cartGrossProfit {
    grid-template-columns: 0fr 2fr;
  }
  .cartGrossProfitBox {
    padding: 2vmax;
    font: 300 2vmax "Roboto";
  }
  .checkOutBtn {
    justify-content: center;
  }
  .checkOutBtn > button {
    padding: 2vmax 4vmax;
    width: 100%;
    font: 300 2vmax "Roboto";
  }
  
.emptyCart {
 
  height: 70vh;
 

}
.emptyCart > svg {
  font-size: 10vmax;
}

.emptyCart > p {
  font: 300 3vmax "Roboto";
  margin: 2vmax;
}


.emptyCart > a {
 
  font: 400 2.5vmax "Roboto";
  
}

}
