.CartItemCard {
  display: flex;
  padding: 1vmax;
  height: 8vmax;
  align-items: flex-start;
  box-sizing: border-box;
}

.CartItemCard > img {
  width: 5vmax;
}
.CartItemCard > div {
  display: flex;
  margin: 0.3vmax 1vmax;
  flex-direction: column;
}

.CartItemCard > div > a {
  font: 300 1.2vmax cursive;
  color: rgba(24, 24, 24, 0.815);
  text-decoration: none;
}

.CartItemCard > div > span {
  font: 300 1.2vmax "Roboto";
  color: rgba(24, 24, 24, 0.815);
}

.CartItemCard > div > p {
  color: tomato;
  font: 800 1vmax "Roboto";
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .CartItemCard {
    padding: 3vmax 0.2vmax;
    height: 25vmax;
  }
  .CartItemCard > img {
    width: 10vmax;
  }
  .CartItemCard > div {
    margin: 1vmax 2vmax;
  }
  .CartItemCard > div > a {
    font: 300 2.2vmax cursive;
  }
  .CartItemCard > div > span {
    font: 300 2.2vmax "Roboto";
  }
  .CartItemCard > div > p {
    font: 300 2vmax "Roboto";
  }
}
