.btn {

    border: none;
    cursor: pointer;
    color: #fff;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 1vmax 'Roboto';
    border-radius: 20px;
    padding: 0.5vmax 2vmax;
    margin: 1vmax;
    outline: none;
}

.btn:hover {
    background-color: rgb(214, 84, 61);
}

@media screen and (max-width:600px) {
    .btn{
        padding: 1.5vmax 3vmax;
    font: 500 2vmax 'Roboto';

    }
}